import { IExtra } from '@interfaces';

export class Extra {
  id: number;
  name: string;
  active: boolean;
  isGlobal: boolean;

  constructor(attrs: IExtra) {
    this.id = attrs.id;
    this.name = attrs.name;
    this.active = attrs.active || false;
    this.isGlobal = attrs.is_global;
  }
}