import { ICustomer } from '../interfaces/customer.interface';

export class Customer {
  id: number;
  full_name: string;
  prefered_name: string;
  phone: string;
  email: string;

  constructor(customer: ICustomer) {
    this.id = customer.id;
    this.full_name = customer.full_name;
    this.prefered_name = customer.prefered_name;
    this.phone = customer.phone;
    this.email = customer.email;
  }

  static empty(): Customer {
    return new this({
      id: 0,
      full_name: '',
      prefered_name: '',
      phone: '',
      email: '',
    });
  }
}
