import { User } from './user.model';
import { IUser } from '@interfaces';

export class Manager extends User {
  constructor(manager: IUser) {
    super(manager as IUser);
  }

  static emptyObject(): Manager {
    return new this({
      id: undefined,
      name: '',
      last_name: '',
      role: '',
      picture: '',
      email: ''
    });
  }
  get fullName(): string {
    return `${this.name} ${this.last_name}`;
  }
}
