import { ReservationZone } from './reservation-zone.model';
import { IOrder, IReservation } from '@interfaces';
import { Customer } from './customer.model';
import * as moment from 'moment';
import { Restaurant } from './restaurant.model';
import { Order } from './order.model';

export class Reservation {
  id: number;
  people: number;
  expires_at: number;
  status: string;
  reservation_at: number;
  customer?: Customer;
  restaurant: Restaurant;
  splited_billing?: false;
  comments?: string;
  zone?: ReservationZone;
  orders?: Array<Order> = [];
  code?: string;
  accepted_at?: number;
  ordersCount: number;
  total_in_cents: number;
  is_new: boolean;
  tips: number;
  rating: {
    id: number;
    score: number;
    comment: string;
  };

  constructor(reservation: IReservation) {
    this.id = reservation.id;
    this.people = reservation.people;
    this.expires_at = reservation.expires_at;
    this.status = reservation.status;
    this.reservation_at = reservation.reservation_at;
    this.customer = reservation.customer
      ? new Customer(reservation.customer)
      : Customer.empty();
    this.restaurant = new Restaurant(reservation.restaurant);
    this.splited_billing = reservation.splited_billing;
    this.comments = reservation.comments;
    this.zone = reservation.zone
      ? new ReservationZone(reservation.zone)
      : ReservationZone.emptyObject();
    this.orders = this.getOrders(reservation.orders);
    this.code = reservation.code;
    this.ordersCount = reservation.ordersCount;
    this.accepted_at = reservation.accepted_at;
    this.tips = reservation.tips;
    this.rating = reservation.rating;
  }

  get customerFullName(): string {
    return this.customer.full_name;
  }

  get customerPhone(): string {
    return this.customer.phone;
  }

  get fullDate(): string {
    moment.locale('es');
    return moment.unix(this.reservation_at / 1000).format('dddd, DD MMMM YYYY');
  }

  get timeLeft(): string {
    moment.locale('es');
    return moment.unix(this.expires_at / 1000).fromNow();
  }

  get fullTime(): string {
    moment.locale('es');
    return moment.unix(this.reservation_at / 1000).format('HH:mm');
  }

  get zoneName(): string {
    return this.zone.name;
  }

  static empty(): Reservation {
    return new this({
      id: null,
      people: 0,
      expires_at: 0,
      status: '',
      reservation_at: 0,
      customer: {
        id: 0,
        full_name: '',
        prefered_name: '',
        phone: '',
        email: '',
      },
      restaurant: {
        coordinates: [],
        description: '',
        email: '',
        name: '',
        phone: '',
      },
      splited_billing: false,
      comments: '',
      zone: {
        id: 0,
        name: '',
        active: false,
      },
      orders: [],
    });
  }

  private getOrders(orders: Array<IOrder>): Array<Order> {
    if (orders && orders.length > 0) {
      const ordersArray = orders.map((order) => new Order(order));

      return ordersArray;
    } else {
      return [];
    }
  }
}
