import { IRestaurant, IExtra, IGalleryImage } from '@interfaces';
import { Parking } from './parking.model';
import { PaymentMethod } from './payment-method.model';
import { ReservationZone } from './reservation-zone.model';
import { ServiceType } from './service-type.model';
import { WorkingHour } from './working-hour.model';
import { Manager } from './manager.model';

export class Restaurant {
  coordinates?: Array<number>;
  address?: string;
  address_extra?: string;
  cover?: IGalleryImage;
  description?: string;
  email?: string;
  extras?: Array<IExtra>;
  franchise?: number;
  gallery?: Array<IGalleryImage>;
  id?: number;
  is_head_office?: boolean;
  max_cancel_limit_minutes?: number;
  max_offered_seats?: number;
  max_people_per_order?: number;
  matterport?: string;
  name: string;
  parking?: Array<Parking>;
  payment_methods?: Array<PaymentMethod>;
  working_hours?: Array<WorkingHour>;
  phone?: string;
  reservation_time_minutes?: number;
  restaurant_working_hours?: Array<WorkingHour>;
  type?: Array<ServiceType>;
  zones?: Array<ReservationZone>;
  managerObject?: Manager;
  manager?: number;
  invoice_email?: string;
  invoice_url?: string;

  constructor(attrs: IRestaurant) {
    this.coordinates = attrs.coordinates ? attrs.coordinates : [];
    this.address = attrs.address;
    this.address_extra = attrs.address_extra;
    this.cover = attrs.cover;
    if (attrs.manager) {
      this.managerObject = new Manager(attrs.manager);
    }
    this.description = attrs.description;
    this.email = attrs.email;
    this.extras = attrs.extras || [];
    this.franchise = attrs.franchise;
    this.matterport = attrs.matterport;
    this.gallery = attrs.gallery;
    this.id = attrs.id;
    this.is_head_office = attrs.is_head_office;
    this.max_cancel_limit_minutes = attrs.max_cancel_limit_minutes;
    this.max_offered_seats = attrs.max_offered_seats;
    this.max_people_per_order = attrs.max_people_per_order;
    this.name = attrs.name;
    this.parking = attrs.parking || [];
    this.payment_methods = attrs.payment_methods || [];
    this.phone = attrs.phone;
    this.reservation_time_minutes = attrs.reservation_time_minutes;
    if (attrs.working_hours) {
      this.working_hours = attrs.working_hours.map((hour) => {
        return new WorkingHour(hour);
      });
    }
    this.type = attrs.type || [];
    this.zones = attrs.zones || [];
    this.invoice_email = attrs.invoice_email;
    this.invoice_url = attrs.invoice_url;
  }

  get restaurantManager(): string {
    return this.managerObject ? this.managerObject.fullName : 'No asignado';
  }
}
