import { IReservationZone } from '@interfaces';

export class ReservationZone {
  id: number;
  name: string;
  active: boolean;

  constructor(attrs: IReservationZone) {
    this.id = attrs.id;
    this.name = attrs.name;
    this.active = attrs.active || false;
  }

  static emptyObject(): ReservationZone {
    return new this({
      id: 0,
      name: '',
      active: false,
    });
  }
}
