import { IDish, IOrderModifier } from '@interfaces';
import { OrderModifier } from './order-modifier.model';

export class Dish {
  id: number;
  name: string;
  price_in_cents: number;
  modifiers: Array<OrderModifier> = [];
  comments: string;
  uid?: string;
  amount?: number;

  constructor(dish: IDish) {
    this.id = dish.id;
    this.name = dish.name;
    this.price_in_cents = dish.price_in_cents;
    this.modifiers = dish.modifier_group.map(
      (mod: IOrderModifier) => new OrderModifier(mod)
    );
    this.uid = dish.uid;
    this.amount = dish.amount;
    this.comments = dish.comments;
  }

  get priceInPesos(): number {
    return this.price_in_cents / 100;
  }
}
