import { IUser } from '@interfaces';

export class User {
  id: number;
  name: string;
  last_name: string;
  role: string;
  picture: string;
  email: string;

  constructor(user: IUser) {
    this.id = user.id;
    this.name = user.name;
    this.last_name = user.last_name;
    this.role = user.role;
    this.picture = user.picture;
    this.email = user.email;
  }
}
