import { IOrderModifier } from '@interfaces';

export class OrderModifier {
  id: number;
  name: string;
  price_in_cents: number;

  constructor(modifier: IOrderModifier) {
    this.id = modifier.id;
    this.name = modifier.name;
    this.price_in_cents = modifier.price_in_cents;
  }

  get priceInPesos(): number {
    return this.price_in_cents / 100;
  }
}
