import { IFranchise } from '@interfaces';
import { Restaurant } from '@models';

export class Franchise {
  id?: number;
  name: string;
  price_range: number;
  style: number;
  restaurants?: Array<Restaurant>;

  constructor(attrs: IFranchise) {
    this.id = attrs.id;
    this.name = attrs.name;
    this.price_range = attrs.price_range;
    this.style = attrs.style;
    this.restaurants = attrs.restaurants;
  }

  static emptyObject(): Franchise {
    return new this({
      id: 0,
      name: '',
      price_range: 0,
      style: 0,
      restaurants: [],
    });
  }
}
