import { INetIncome } from '@interfaces';

export class NetIncome {
  total: number;
  taxes: number;
  fees: number;
  income: number;
  tips: number;
  tips_fees: number;

  constructor(netIncome: INetIncome) {
    this.total = netIncome.total;
    this.taxes = netIncome.taxes;
    this.fees = netIncome.fees;
    this.income = netIncome.income;
    this.tips = netIncome.tips;
    this.tips_fees = netIncome.tips_fees;
  }

  static emptyObject(): NetIncome {
    return new this({
      total: 0,
      taxes: 0,
      fees: 0,
      income: 0,
      tips: 0,
      tips_fees: 0,
    });
  }
}
